<template>
    <div class="content" :class="{'blur-content': showDialog}">
      <CCard>
        <CCardBody>
          <CRow class="row-content">
              <c-col sm="6" align="left"><h4>DAFTAR ANGGOTA</h4></c-col>
              <c-col sm="6" align="right">Terdapat <b>{{totalData}}</b> Anggota</c-col>
          </CRow>
          <CRow>
              <c-col sm="2" align="left">
                  <CSelect
                      :options="['Pangkat','Kepala','Staff']"
                  />
              </c-col>
              <c-col sm="2" align="left">
                  <CSelect
                      :options="['Satatus','Aktif','Tidak Aktif']"
                  />
              </c-col>
              <c-col sm="4"></c-col>
              <c-col sm="4 input-box-search">
                <md-field style="padding:0px;">
                  <md-input placeholder="CARI" v-model="searched"></md-input>
                  <CButton @click="searchData"><md-icon>{{searchIcon}}</md-icon></CButton>
                </md-field>
              </c-col>
          </CRow>
          <CRow>
              <c-col sm="12">
                <md-table v-model="data" md-card @md-selected="onSelect">
                  <md-table-toolbar slot="md-table-alternate-header" slot-scope="{ count }">
                    <div class="md-toolbar-section-start">{{ getAlternateLabel(count) }}</div>
                    <div class="md-toolbar-section-end">
                      <md-button class="md-icon-button">
                        <md-icon>delete</md-icon>
                      </md-button>
                    </div>
                  </md-table-toolbar>
                  <md-table-row slot="md-table-row" slot-scope="{ item }" md-selectable="multiple">
                    <md-table-cell md-label="ID" md-sort-by="id">{{ item.id }}</md-table-cell>
                    <md-table-cell md-label="NAMA DEPAN" md-sort-by="nama_depan">{{ item.nama_depan }}</md-table-cell>
                    <md-table-cell md-label="NAMA BELAKANG" md-sort-by="nama_belakang">{{ item.nama_belakang }}</md-table-cell>
                    <md-table-cell md-label="NRP" md-sort-by="nrp">{{ item.nrp }}</md-table-cell>
                    <md-table-cell md-label="PANGKAT" md-sort-by="pangkat">{{ item.nrp }}</md-table-cell>
                    <md-table-cell md-label="SEBAGAI KEPALA" md-sort-by="sebagai_kepala">{{ item.sebagai_kepala }}</md-table-cell>
                    <md-table-cell md-label="STATUS" md-sort-by="status">{{ item.nrp }}</md-table-cell>
                    <md-table-cell md-sort-by="id">
                      <md-menu md-direction="bottom-start">
                        <md-button md-menu-trigger><md-icon>more_vert</md-icon></md-button>
                        <md-menu-content>
                          <md-menu-item @click="openDialog">Edit</md-menu-item>
                          <md-menu-item>Non Aktifkan</md-menu-item>
                        </md-menu-content>
                      </md-menu>
                    </md-table-cell>
                  </md-table-row>
                </md-table>
              </c-col>
          </CRow>
          <CRow style="margin-top:1rem;margin-bottom:3rem;">
              <CCol sm="12" align="right">
                <CButton><md-icon>chevron_left</md-icon></CButton> Halaman <b>1</b> dari 2 <CButton><md-icon>chevron_right</md-icon></CButton>
              </CCol>
          </CRow>
          <CRow>
            <c-col align="right"> 
              <md-button class="md-fab md-primary" @click="openDialog" style="background-color:green;">
                <md-icon>add</md-icon>
              </md-button>
            </c-col>
          </CRow>
        </CCardBody>
      </CCard>

      <md-dialog :md-active.sync="showDialog" :md-fullscreen="true" :md-backdrop="false">
        <md-dialog-title><h4>TAMBAH ANGGOTA BARU</h4></md-dialog-title>

        <md-dialog-content>
        <CRow>
          <CCol sm="6 input-box">
            <md-field>
              <md-input placeholder="Nama Depan*"></md-input>
            </md-field>
          </CCol>
          <CCol sm="6 input-box">
            <md-field>
              <md-input placeholder="Nama Belakang"></md-input>
            </md-field>
          </CCol>
        </CRow>
        <CRow>
          <CCol sm="6 input-box">
            <md-field>
              <md-input placeholder="NRP*"></md-input>
            </md-field>
          </CCol>
          <CCol sm="6 input-box">
            <md-field>
              <md-input placeholder="Posisi / Jabatan*"></md-input>
            </md-field>
          </CCol>
        </CRow>
        <CRow>
          <CCol sm="6 input-box">
            <md-field>
              <label for="movie">Pilih POLDA</label>
              <md-select>
                <md-option value="fight-club">Polda Jabar</md-option>
                <md-option value="godfather">Polda Jateng</md-option>
              </md-select>
            </md-field>
          </CCol>
          <CCol sm="6 input-box">
            <md-field>
              <label for="movie">Pilih POLRES</label>
              <md-select>
                <md-option value="fight-club">Polres Jabar</md-option>
                <md-option value="godfather">Polres Jateng</md-option>
              </md-select>
            </md-field>
          </CCol>
        </CRow>
        <CRow>
          <CCol sm="6 input-box">
            <md-field>
              <md-input placeholder="Sebagai Kepala"></md-input>
            </md-field>
          </CCol>
          <CCol sm="6 input-box">
            <md-field>
              <md-input placeholder="Pangkat"></md-input>
            </md-field>
          </CCol>
        </CRow>
        <CRow>
          <CCol sm="6" align="left">
            <md-button class="md-raised">SIMPAN</md-button>
          </CCol>
          <CCol sm="6" align="right" @click="batalDialog">
            <md-button>BATAL</md-button>
          </CCol>
        </CRow>
        </md-dialog-content>

      </md-dialog>

    </div>
</template>

<script>

import axios from 'axios';
export default {
  name: 'AdvancedTables',
  data () {
    return {
      searched:'',
      showDialog: false,
      selected: [],
      data: [],
      searchIcon:null,
      totalData:'0'
    }
  },
  mounted(){
    axios.get(`../data.json`).then(response => {
      this.data = response.data.data_anggota.data
      this.searchIcon = 'search'
      this.totalData = response.data.data_anggota.total_data
    })
  },
  methods: {
    onSelect (items) {
      this.selected = items
    },
    getAlternateLabel (count) {
      let plural = ''

      if (count > 1) {
        plural = 's'
      }

      return `${count} user${plural} selected`
    },
    searchData(){
      // console.log(this.searched)

      // the code you're looking for
      var search = this.searched;

      const dataSearch = []
      for (var i = 0; i < this.data.length; i++){
        if (this.data[i].id == search || this.data[i].nama_depan == search || this.data[i].nama_belakang == search || this.data[i].nrp == search || this.data[i].pangkat == search || this.data[i].sebagai_kepala == search || this.data[i].status == search){
          dataSearch.push(this.data[i]);
        }
      }


      this.data = dataSearch


      if(this.searchIcon == 'clear'){
        this.searchIcon = 'search'
        this.searched = null
        axios.get(`../data.json`).then(response => {
          this.data = response.data.data_anggota.data
        })
      } else {
        this.searchIcon = 'clear'
      }



     
    },
    openDialog(){
      this.showDialog=true
      this.$store.commit('toggleSidebarDesktop')
    },
    batalDialog(){
      this.showDialog=false
      this.$store.commit('toggleSidebarDesktop')
    }
  }
}
</script>